<template>
	<div class="topic-list">
		<Scroll :on-reach-bottom="handleReachBottom" :on-reach-top="handleReachTop">
			<div class="cell" v-for="(item, index) in allArray" :key="index">
				<router-link :to="'/User/' + item.author.loginname" class="user_avatar pull-left">
					<img :src="item.author.avatar_url" :title="item.author.loginname" />
				</router-link>
				<span class="reply_count pull-left">
						    <span class="count_of_replies" title="回复数">{{ item.reply_count }}</span>
				<span class="count_seperator">/</span>
				<span class="count_of_visits" title="点击数">{{ item.visit_count }}</span>
				</span>
				<a class="last_time pull-right" href="/topic/58abbd21a9c1282817afc28d#59e716d6cd6234a16d54841c">
					<span class="last_active_time">{{ item.last_reply_at | filtrationTime }}</span>
				</a>
				<div class="topic_title_wrapper">
					<span class="topiclist-tab" v-if="!item.top">{{ tabs[item.tab] }}</span>
					<span class="put_top" v-else>置顶</span>
					<router-link class="topic_title" :to="'/Topic/' + item.id" :title="item.title">
						{{ item.title }}
					</router-link>
				</div>
			</div>
		</Scroll>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				allArray: [],
				nextArray: [],
				pages: 1,
				nowTime: Date.now(),
				lastReplyTime: 0,
				tabs: {
					share: '分享',
					good: '精华',
					weex: 'weex',
					ask: '问答',
					job: '招聘'
				},
				scrollTop: 0
			}
		},
		methods: {
			handleReachBottom () {
                return new Promise(resolve => {
                	this.pages++
                    setTimeout(() => {
						var that = this
						this.axios.get('https://www.vue-js.com/api/v1/topics', {
							params: {
								tab: 'all',
								page: that.pages
							}
						})
						.then(function(response) {
							that.nextArray = response.data.data;
							that.allArray = that.allArray.concat(that.nextArray);
						})
						.catch(function(error) {
							console.log(error);
						});
                        resolve();
                    }, 2000);
                });
            },
            handleReachTop() {
				return new Promise(resolve => {
					this.pages = 1;
					setTimeout(() => {
						var that = this
						this.axios.get('https://www.vue-js.com/api/v1/topics', {
							params: {
								tab: 'all',
								page: that.pages
							}
						})
						.then(function(response) {
							that.allArray = response.data.data;
						})
						.catch(function(error) {
							console.log(error);
						});
                        resolve();
					}, 2000);
				});
			},
		},
		created() {
			var that = this
			this.axios.get('https://www.vue-js.com/api/v1/topics', {
				params: {
					tab: 'all'
				}
			})
			.then(function(response) {
				that.allArray = response.data.data;
				console.log(that.allArray)
				that.lastReplyTime = new Date(response.data.data[0].last_reply_at).getTime();
			})
			.catch(function(error) {
				console.log(error);
			});
		},
		mounted() {
//			console.log(document.getElementsByClassName("ivu-scroll-container")[0])
		}
	}
</script>
<style>

</style>


