<template>
	<div id="content">
		<div class="panel">
			<div class="header">
				<router-link to="/" class="home">主页</router-link>
				<span class="diver">/</span>
			</div>
			<div class="inner userinfo">
				<div class="user_big_avatar">
					<img :src="userData.avatar_url" alt="" />
				</div>
				<a class="user_loginname" href="">{{ userData.loginname }}</a>
				<div class="user_profile">
					<span class="big">{{ userData.score }} 积分</span>
					<div class="user_home" v-if="userData.githubUsername">
						<i class="iconfont icon-home-copy"></i>
						<a :href="'https://github.com/' + userData.githubUsername" target="_blank">
							https://github.com/{{ userData.githubUsername }}
						</a>
					</div>
					<div class="user_github" v-if="userData.githubUsername">
						<i class="iconfont icon-github-copy"></i>
						<a :href="'https://github.com/' + userData.githubUsername" target="_blank">
							@{{ userData.githubUsername }}
						</a>
					</div>
				</div>
				<p class="col_fade">注册时间</p>
			</div>
		</div>
		<div class="panel">
			<div class="header">
				<a class="col_fade">最近创建的话题</a>
			</div>
			<div class="cell"  v-for="(build, index) in userData.recent_topics" :key="'build' + index">
				<router-link :to="'#/User/' + build.author.loginname" class="user_avatar pull-left">
					<img :src="build.author.avatar_url" title="febobo">
				</router-link>
				<span class="reply_count pull-left">
					<span title="回复数" class="count_of_replies">32</span>
					<span class="count_seperator">/</span>
					<span title="点击数" class="count_of_visits">3108</span>
				</span>
				<a href="/topic/58abbd21a9c1282817afc28d#59e716d6cd6234a16d54841c" class="last_time pull-right">
					<span class="last_active_time">2天前</span>
				</a>
				<div class="topic_title_wrapper">
					<span class="put_top">置顶</span>
					<a href="#/Topic/58c216c0b011a6b41c0261fa" class="topic_title" :title="build.title">
						{{ build.title }}
					</a>
				</div>
			</div>
		</div>
		<div class="panel">
			<div class="header">
				<a class="col_fade">最近参与的话题</a>
			</div>
			<div class="cell"  v-for="(participation, index) in userData.recent_replies" :key="'participation' + index">
				<router-link :to="'#/User/' + participation.author.loginname" class="user_avatar pull-left">
					<img :src="participation.author.avatar_url" title="febobo">
				</router-link>
				<span class="reply_count pull-left">
					<span title="回复数" class="count_of_replies">32</span>
					<span class="count_seperator">/</span>
					<span title="点击数" class="count_of_visits">3108</span>
				</span>
				<a href="/topic/58abbd21a9c1282817afc28d#59e716d6cd6234a16d54841c" class="last_time pull-right">
					<span class="last_active_time">2天前</span>
				</a>
				<div class="topic_title_wrapper">
					<span class="put_top">置顶</span>
					<a href="#/Topic/58c216c0b011a6b41c0261fa" class="topic_title" :title="participation.title">
						{{ participation.title }}
					</a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'User',
		data() {
			return {
				userData: {}
			}
		},
		created() {
			var newUser = []
			var that = this
			this.axios.get('https://www.vue-js.com/api/v1/user/' + that.$route.params.loginname, {
					params: {}
				})
				.then(function(response) {
					that.userData = response.data.data;
					newUser = response.data.data.recent_replies
					console.log(newUser)
					console.log(that.userData)
				})
				.catch(function(error) {
					console.log(error);
				});
		}
	}
</script>

<style>
	.header>a {
		color: #369219;
	}
	
	.diver {
		padding: 0 5px;
		color: #ccc;
	}
	
	.userinfo {
		padding: 10px;
		border-top: 1px solid #e5e5e5;
	}
	
	.panel .inner {
		line-height: 2em;
		background-color: #fff;
		padding: 10px;
		border-radius: 0 0 3px 3px;
	}
	
	.user_big_avatar {
		float: left;
		margin-right: 10px;
	}
	
	.user_big_avatar img {
		width: 40px;
	}
	
	.user_loginname {
		color: #778087;
	}
	
	.user_profile {
		clear: left;
		margin-bottom: 10px;
		color: #778087;
	}
	
	.user_profile a {
		clear: left;
		margin-bottom: 10px;
		color: #778087;
	}
	
	.header .col_fade {
		color: #1c6132;
	}
</style>


