// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
//import ElementUI from 'element-ui'
import axios from 'axios'
//import 'element-ui/lib/theme-default/index.css'
import iView from 'iview';
import 'iview/dist/styles/iview.css';
//Vue.use(ElementUI)

Vue.prototype.axios = axios;

Vue.use(iView);

Vue.config.productionTip = false

Vue.filter('filtrationTime', function (val) {
				var nowTime = Date.now();
				var lastReplyTime = new Date(val).getTime();
				var timeDifference = nowTime - lastReplyTime;
				var timeDifYear = Math.floor(timeDifference / 1000 / 60 / 60 / 24 / 365);
				var timeDifMonth = Math.floor(timeDifference / 1000 / 60 / 60 / 24 / 30);
				var timeDifDate = Math.floor(timeDifference / 1000 / 60 / 60 / 24);
				var timeDifHour = Math.floor(timeDifference / 1000 / 60 / 60);
				var timeDifMinute = Math.floor(timeDifference / 1000 / 60);
				var timeDifSecond = Math.floor(timeDifference / 1000);
				if(timeDifYear > 0) {
					return timeDifYear + '年前';
				} else if(timeDifMonth > 0) {
					return timeDifMonth + '个月前';
				} else if(timeDifDate > 0) {
					return timeDifDate + '天前';
				} else if(timeDifHour > 0) {
					return timeDifHour + '小时前';
				} else if(timeDifMinute > 0) {
					return timeDifMinute + '分钟前';
				} else if(timeDifSecond > 0) {
					return timeDifSecond + '秒前';
				}
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  render: h => h(App)
})



