import Vue from 'vue'
import Router from 'vue-router'
import ContentHead from '@/components/ContentHead'
import All from '@/components/Classify/All'
import Good from '@/components/Classify/Good'
import Weex from '@/components/Classify/Weex'
import Share from '@/components/Classify/Share'
import Ask from '@/components/Classify/Ask'
import Job from '@/components/Classify/Job'
import Topic from '@/components/Topic'
import User from '@/components/User'

Vue.use(Router)


export default new Router({
	routes: [
		{
			path: '/',
			component: ContentHead,
			redirect: '/ContentHead/Classify/All'
		},
		{
			path: '/ContentHead',
	//		name: 'ContentHead',
			component: ContentHead,
			children: [
				{ path: '/', component: ContentHead},
				{ path: 'Classify/All', name: 'All', component: All},
				{ path: 'Classify/Good', name: 'Good', component: Good},
				{ path: 'Classify/Weex', name: 'Weex', component: Weex},
				{ path: 'Classify/Share', name: 'Share', component: Share},
				{ path: 'Classify/Ask', name: 'Ask', component: Ask},
				{ path: 'Classify/Job', name: 'Job', component: Job}
			]
		},
		{
			path: '/Topic/:id',
			name: 'Topic',
			component: Topic
		},
		{
			path: '/User/:loginname',
			name: 'User',
			component: User
		}

	]
})


