<template>
	<div id="content">
		<div class="panel">
			<div class="header tpc_header">
				<span class="topic_full_title">
					<span class="put_top">置顶</span> {{ dataHtml.title }}
				</span>
				<div class="changes">
					<span>发布于{{ dataHtml.create_at | filtrationTime}}</span>
					<span>
						作者
						<router-link to="">{{ loginname }}</router-link>
					</span>
					<span>{{ dataHtml.visit_count}} 次浏览</span>
					<span>最后一次编辑是 {{ dataHtml.last_reply_at | filtrationTime}}</span>
					<span>来自 {{ tabs[dataHtml.tab] }}</span>
				</div>
			</div>
			<div class="tpc_content" v-html="dataHtml.content"></div>
		</div>
		<div class="panel">
			<div class="tpc_replies">
				<div class="header replies_header">
					<span v-if="dataHtml">{{ dataHtml.replies.length }} 回复</span>
				</div>
				<div class="replies_content">
					<div class="cell reply_item" v-for="(item, index) in dataHtml.replies" :key="'replis' + index">
						<div class="author_content">
							<a href="">
								<img :src="item.author.avatar_url" />
							</a>
							<div class="user_info">
								<a class="reply_loginname" href="">{{ item.author.loginname }}</a>
								<a class="reply_time" href="">{{ index + 1 }}楼·几个月前</a>
							</div>
							<div class="user_action" v-if="item.ups.length">
								<span>
									<i class="iconfont icon-icon10"></i>
									<span>{{ item.ups.length }}</span>
								</span>
							</div>
						</div>
						<div class="author_reply">
							<p v-html="item.content"></p>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div id="goTop-tpc" @click="goTopTpc">回到顶部</div>
	</div>
</template>

<script>
	var divGoTopTpc = null;
	export default {
		name: 'Topic',
		data() {
			return {
				dataHtml: '',
				tabs: {
					share: '分享',
					good: '精华',
					weex: 'weex',
					ask: '问答',
					job: '招聘'
				},
				loginname: ''
			}
		},
		methods: {
			goTopTpc() {
				var timer = null;
				var speed = 0;
				timer = setInterval(function() {
					//滚动条高度
//					divScrollTop = scrollContainer.scrollTop;
					speed = Math.floor((-document.documentElement.scrollTop) / 5);
					document.documentElement.scrollTop = document.documentElement.scrollTop + speed;
					if(document.documentElement.scrollTop == 0) {
						clearInterval(timer);
					}
				}, 10);
			}
		},
		created() {
			var that = this
			this.axios.get('https://www.vue-js.com/api/v1/topic/' + that.$route.params.id, {
				params: {}
			})
			.then(function(response) {
				that.dataHtml = response.data.data;
				that.loginname = response.data.data.author.loginname
				console.log(that.dataHtml)
			})
			.catch(function(error) {
				console.log(error);
			});
		},
		mounted() {
			divGoTopTpc = document.getElementById("goTop-tpc");
			divGoTopTpc.style.display = "none";
			window.onscroll = function () {
				if (document.documentElement.scrollTop > 10) {
					divGoTopTpc.style.display = "block";
				}else {
					divGoTopTpc.style.display = "none";
				}
			}
		}
	}
</script>

<style>
	.reply_item a {
		display: inline-block;
		float: left;
	}
	.reply_item img {
		width: 30px;
		height: 30px;
		border-radius: 3px;
	}
	.author_content {
		overflow: hidden;
	}
	.user_info {
		float: left;
		margin-left: 10px;
	}
	.reply_loginname {
		color: #666;
		font-size: 12px;
		font-weight: 700;
	}
	.reply_time {
		font-size: 11px;
		color: #08e;
	}
	
	.user_action {
		float: right;
	}
	
	.author_reply {
		padding-top: 5px;
	}
	
	.author_reply p {
		font-size: 15px;
		margin-bottom: 15px;
	}
</style>


