<template>
	<div>
		<div id="goTop" @click="goTop">回到顶部</div>
		<div class="content-head">
			<router-link to="/">全部</router-link>
			<router-link to="/ContentHead/Classify/Good">精华</router-link>
			<router-link to="/ContentHead/Classify/Weex">weex</router-link>
			<router-link to="/ContentHead/Classify/Share">分享</router-link>
			<router-link to="/ContentHead/Classify/Ask">问答</router-link>
			<router-link to="/ContentHead/Classify/Job">招聘</router-link>
		</div>
		<div class="content-ct">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
//	import axios from 'axios'
	//div滚动条高度
	var divScrollTop = 0;
	//滚动条所在的块
	var scrollContainer = null;
	//回到顶部按钮
	var divGoTop = null;
	export default {
		name: 'ContentHead',
		data() {
			return {
			}
		},
		methods: {
			goTop() {
				var timer = null;
				scrollContainer = document.getElementsByClassName("ivu-scroll-container")[0];
				var speed = 0;
				timer = setInterval(function() {
					//滚动条高度
					divScrollTop = scrollContainer.scrollTop;
					speed = Math.floor((-divScrollTop) / 5);
					scrollContainer.scrollTop = divScrollTop + speed;
					if(divScrollTop == 0) {
						clearInterval(timer);
						document.documentElement.scrollTop = document.body.scrollTop = 0;
					}
				}, 10);
			}
		},
		mounted() {
			scrollContainer = document.getElementsByClassName("ivu-scroll-container")[0];
			divGoTop = document.getElementById("goTop");
			scrollContainer.onscroll = function () {
				divScrollTop = scrollContainer.scrollTop;
				if (divScrollTop > 20) {
					divGoTop.style.display = "block";
				}else {
					divGoTop.style.display = "none";
				}
			}
		},
		updated () {
			scrollContainer = document.getElementsByClassName("ivu-scroll-container")[0];
			divGoTop = document.getElementById("goTop");
			divGoTop.style.display = "none";
			scrollContainer.onscroll = function () {
				divScrollTop = scrollContainer.scrollTop;
				if (divScrollTop > 20) {
					divGoTop.style.display = "block";
				}else {
					divGoTop.style.display = "none";
				}
			}
		}
	}
	
</script>

<style>
	.content-head {
		padding: 10px;
		background-color: #f6f6f6;
		border-radius: 3px 3px 0 0;
		font-size: 0;
	}
	
	.content-head>a {
		font-size: 14px;
		margin: 0 10px;
		color: #369219;
	}
	.content-ct {
		padding: 0;
	}
</style>




